import * as React from 'react';
import { Fragment } from 'react';
// import { useCreateCommunity } from '../assets/helper/index';
import Box from '../components/Box';
import Form from '../components/Form';

interface IProps {
    setcommunitySymbol: any;
    setcommunityName: any;
    setOnboarding: any;
    boarding: number;
}

export default ({ setcommunityName, setcommunitySymbol, boarding, setOnboarding }: IProps) => {

    return (
        <Fragment>
            {
                boarding === 1 && (
                    <Fragment>
                        <Box title="Create a community name">
                            <Form
                                title='Enter community name'
                                onSubmit={(text: any) => {
                                    setcommunityName(text)
                                    setOnboarding(2)
                                }}
                                onClick={() => console.log('hello')}
                            />
                        </Box>
                    </Fragment>
                )
            }

            {
                boarding === 2 && (
                    <Fragment>
                        <Box title="Create a community Symbol">
                            <Form
                                title='Enter community Symbol'
                                onSubmit={(text: any) => {
                                    setcommunitySymbol(text)
                                    setOnboarding(3)
                                }}
                                onClick={() => console.log('hello')}
                            />
                        </Box>
                    </Fragment>
                )
            }
        </Fragment>
    )
}